@import "../../bootstrap/mixins/border-radius";

.workspace{
    margin:45px 0 10px 0;
    z-index:809000;
}

.editable-area{
    position:relative;
    width:960px;
    margin:0 auto;
    border-left:1px dashed #ccc;
    border-right:1px dashed #ccc;
    height:100%;
}

.page-item{
    position: absolute;
}

.page-item:hover{
    outline:1px solid #ccc;
}